

/* Button */
.buttonNext {
    background-color: white;
    border: 0;
    color: black; 
    padding: 16px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s;
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    cursor: pointer;
}

.buttonNext:hover {
    background-color: #6c6c6c;
    color: white;
}



